.aboutbg{
    background-color: #f5f5f5;
    margin-top: 4em;
    padding: 2em 1em 1em 1em;
    width: 100%;
    .about-head{
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 36px;
        color: #233142;
        font-weight: 700;
        padding: 10px;
    }
    .grid-about{
        display: grid;
        grid-template-columns: 1fr;
        margin: 2em 0em 2em 0em;
        .grid-img{
            display: flex;
            align-items: center;
            justify-content: center;
            padding: 10px;
            img{
                width: 90%;
                border-radius: 10px;
            }
        }
        .detailed{
            display: flex;
            flex-direction: column;
            align-items: left;
            justify-content: center;
            padding: 20px;
            padding-left: 2.5em;
            .about-text{
                font-size: 18px;
                color: #233142;
                font-weight: 400;
                padding: 10px;
                line-height: 1.75;
            }
        }
    }
}

@media all and(min-width:776px){
    .aboutbg{
        margin-top: 6.6em;
        .grid-about{
            grid-template-columns: 1fr 1fr;
            margin-top:4em;
            .grid-img{
                img{
                    width:70%;
                }
            }
            .detailed{
                justify-content: flex-start;
                padding-left: 0em;
                .about-text{
                    padding: 0em;
                    padding-right: 1em;
                    font-size: 1.6rem;
                    opacity: 65%;
                }
            }
        }
    }
}